.appContainer {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  background-color: #f8f8f8;
  overflow-x: hidden;
  background-color: #E6E7E9;
}

.appContainer:global(.darkTheme) {
  background-color: #333333;
  color: white;
}

.controls {
  background-color: #f8f8f8;
  width: 19rem;
  padding: 0.5rem;
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  -o-transition: transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

:global(.darkTheme) .controls {
  background-color: #666666;
}

.controls .section {
  border-top: 2px dashed rgba(125, 125, 125, 0.5);
  padding: 1rem 0;
  -ms-flex: 0 0;
      flex: 0 0;
}

.controls .hierarchySection {
  overflow: auto;
  -ms-flex: 0 1 auto;
      flex: 0 1 auto;
}

.canvas {
  -ms-flex: 1 1;
      flex: 1 1;
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  -o-transition: transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}

input[name=hideControls] {
  display: none;
}

label.hideControls {
  position:absolute;
  z-index: 10;
  top: 0;
  left: 16.5rem;
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  -o-transition: transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  background-color: #f8f8f8;
  cursor: pointer;
  padding: 0;
}

:global(.darkTheme) label.hideControls {
  background-color: #666666;
}

label.hideControls span {
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  text-align: center;
  display: block;
  -webkit-transform: scale(0.5, 1);
          transform: scale(0.5, 1);
  font-weight: bold;
}

input[name=hideControls]:checked ~ label.hideControls {
  -webkit-transform: translate3d(-16.5rem, 0, 0);
          transform: translate3d(-16.5rem, 0, 0);
}

input[name=hideControls]:checked ~ label.hideControls span {
  -webkit-transform: rotate(-180deg) scale(0.5, 1);
          transform: rotate(-180deg) scale(0.5, 1);
}

input[name=hideControls]:checked ~ .controls {
  -webkit-transform: translate3d(-18.5rem, 0, 0);
          transform: translate3d(-18.5rem, 0, 0);
}

input[name=hideControls]:checked ~ .canvas {
  -webkit-transform: translate3d(-18.5rem, 0, 0);
          transform: translate3d(-18.5rem, 0, 0);
  margin-right: -18.5rem;
}



